<template>
  <transition name="fade">
    <div class="toast" v-show="isShow">{{message}}</div>
  </transition>
</template>

<script>
	export default {
		name: "Toast",
    data() {
			return {
				isShow: false,
        message: '',
      }
    },
    methods: {
      show(message, duration) {
        this.isShow = true
        this.message = message

        setTimeout(() => {
          this.isShow = false
          this.message = message
        }, duration)
      }
    }
	}
</script>

<style scoped>
  .toast {
    position: fixed;
    left: 50%;
    top: 50%;
    background-color: rgba(0,0,0,.8);
    padding: 1.9vw;
    border-radius: 0.3vw;
    transform: translate(-50%,-50%);
    color:#fff;
    z-index: 99;
  }

  .fade-enter-active, .fade-leave-active {
    transition: opacity .5s;
  }
  .fade-enter, .fade-leave-to {
    opacity: 0;
  }
</style>